@media screen and (min-width: 1025px) {
  .input {
    width: 50vw !important;
  }
}
.nav {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-content: center;
  margin-top: 5rem;
  width: 100vw;
}
.nav button {
  display: none;
}
.nav .input {
  margin-top: 0.5rem;
  height: 55px;
  width: 80vw;
  border: none;
  border-radius: 30px;
  font-family: "FontAwesome", "Poppins", sans-serif;
  font-size: 1.3rem;
  padding: 1.5rem;
  background-color: #C1E9E3;
}
.nav .searchi {
  display: none;
}

