@import '../../styles/break-point.sass'

@include desktop
  .vehicleCard
    width: 30vw !important
    height: 25vw !important


.vehicleCard
  display: flex
  padding-left: 1rem
  flex-direction: column
  justify-content: space-evenly 
  margin-top: 2em
  width: 85vw
  height: 35vh
  background-color: #F24957

.icons
  display: flex
  justify-content: right
  background-color: #F24957
  
.icon
  height: 2em
  background-color: #F24957
  margin-right: 1em
  cursor: pointer

.infos
  h2, h3, p
    background-color: #F24957
    color: white

#heart
  visibility: hidden
  margin-right: 2.5em


#heart:before
  visibility: visible
  font-family: "FontAwesome"
  font-size: 2.5em
  font-weight: 900
  content: "\f004"
  color: black

#heart:checked:before
  color: #73D9C6